import React from "react";
import Layout from "./Layout";

const styles = {
  background: "black",
  height: "1000px",
  color: "white",
  fontSize: "5em",
  textAlign: "center"
};

const View = () => {
  return (
    <Layout title="View">
      <div style={styles}>View</div>
    </Layout>
  );
};

export default View;
